// ラッパー
.wrapper {
  position: relative;
  width: 100%;
  height: 100vh;
}

// ヘッダー
.header {
  position: relative;
  height: 50px;
  color: #fff;
  background-color: #777;
}
.btn-menu {
  position: absolute;
  top: 15px;
  right: 15px;
  display: block;
}
@media (min-width: 481px) {
  .btn-menu {
    display: none;
  }
}

// スティッキーヘッダー
.header-fixed {
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  width: 100%;
  height: 50px;
  padding: 15px;
  color: #fff;
  background-color: #999;
  box-sizing: border-box;
  z-index: 9999;
}

// ドロワーメニュー
.drawer {
  position: fixed;
  top: 50px;
  right: 0;
  width: 100%;
  height: calc(100vh - 50px);
  z-index: 1000;
  background-color: rgba(0, 0, 0, .5);
  transition: all .2s;
  transform: translate(100%);
  &.open {
    transform: translate(0);
  }
}
